	module.exports = {"data":{"wordpressPost":{"title":"First impact factor announced: 1.583","content":"<p>&nbsp;</p>\n<p><span style=\"font-family: Calibri;color: #000000;font-size: medium\"><em>BMJ Open</em>’s first impact factor has been announced: 1.583. We are delighted to have this further evidence that <em>BMJ Open</em> is considered a journal of credible, valued research.</span></p>\n<p><b><span style=\"color: #000000\"><span style=\"font-size: medium\"><span style=\"font-family: Calibri\">Does a journal’s impact factor matter?</span></span></span></b></p>\n<p><span style=\"font-family: Calibri;color: #000000;font-size: medium\">In short – yes. When we surveyed our authors earlier this year, we asked what improvements we could make to <em>BMJ Open</em>. By far the most frequent response was: get an impact factor. The impact factor is over-interpreted, misinterpreted and almost certainly too influential. For as long as it remains important to authors, though, it will remain something journals must promote.</span></p>\n<p><span style=\"font-family: Calibri;color: #000000;font-size: medium\">The question of whether a journal’s impact factor <em>should</em> matter, and how much, has been discussed for years. The San Francisco Declaration on Research Assessment (or <a href=\"http://am.ascb.org/dora/\">DORA</a>) highlights some of the issues. </span></p>\n<p><span style=\"font-family: Calibri;color: #000000;font-size: medium\">Unlike many journals, <em>BMJ Open</em> doesn’t attempt to select articles on the strength of their likely citation count. So we&#8217;ll never have the highest in our field, and you won&#8217;t find us worrying about that.</span></p>\n<p><span style=\"color: #000000\"><span style=\"font-size: medium\"><span style=\"font-family: Calibri\">In the future our impact factor may go up or it may go down. Our influence over this will go no further than our continued efforts to ensure <em>BMJ Open</em> publishes thoroughly peer-reviewed open access research and serving all our authors as best we can, so we can build on our reputation as a reliable publishing choice for researchers.  </span></span></span></p>\n<p><b><span style=\"color: #000000\"><span style=\"font-size: medium\"><span style=\"font-family: Calibri\">Article-level impact</span></span></span></b></p>\n<p><span style=\"font-family: Calibri;color: #000000;font-size: medium\">The impact factor is an aggregate measure of <em>BMJ Open</em>’s articles and so says nothing about any individual paper. This is why we also publish article-level metrics. Alongside every paper we publish you can see its abstract, HTML and PDF views, and citations to the article from elsewhere on the HighWire publishing platform. </span></p>\n<p><span style=\"font-family: Calibri;color: #000000;font-size: medium\">Such so-called ‘altmetrics’ are increasingly popular. There’s a wealth of information about these available, and the </span><a href=\"http://blogs.bmj.com/bmj-journals-development-blog/\"><span style=\"font-family: Calibri;font-size: medium\">BMJ Web Development Blog</span></a><span style=\"font-family: Calibri;color: #000000;font-size: medium\"> is a great place to find out more and keep up to date on these. </span></p>\n<p><span style=\"color: #000000\"><span style=\"font-size: medium\"><span style=\"font-family: Calibri\">It is important to remember that research impact extends well beyond an article’s citation rate. This is especially so in clinical research where impact on public health and clinical care cannot be captured by bibliographic measures.  </span></span></span></p>\n<p><b><span style=\"color: #000000\"><span style=\"font-size: medium\"><span style=\"font-family: Calibri\">What is the impact factor? </span></span></span></b></p>\n<p><span style=\"font-family: Calibri;color: #000000;font-size: medium\">The impact factor is a journal-level citation metric. It is usually calculated over three years, by adding the number of articles a journal publishes in years 1 and 2, and seeing how often, on average, these articles were cited in year 3. As <em>BMJ Open</em> only has two years’ worth of citation information, our impact factor was calculated using the number of articles we published in 2011 (which Thomson Reuters calculated as  151) and the number of times they were cited in 2012 (239). 239/151 = 1.583</span></p>\n<p><b><span style=\"color: #000000\"><span style=\"font-size: medium\"><span style=\"font-family: Calibri\">Most-cited from 2011</span></span></span></b></p>\n<p><span style=\"font-family: Calibri;color: #000000;font-size: medium\">The following are the papers we published in 2011 with the most citations in the Thomson Reuters Web of Science in 2012:</span></p>\n<p><span style=\"color: #000000\"><span style=\"font-size: medium\"><span style=\"font-family: Calibri\">Armstrong PK, Dowse GK, Effler PV, <em>et al</em>. Epidemiological study of severe febrile reactions in young children in Western Australia caused by a 2010 trivalent inactivated influenza vaccine. <em>BMJ Open</em> 2011;<strong>1</strong>:e000016. doi:<a href=\"http://dx.doi.org/10.1136/bmjopen-2010-000016\" target=\"pmc_ext\">10.1136/bmjopen-2010-000016</a> </span></span></span></p>\n<p><span style=\"font-family: Calibri;color: #000000;font-size: medium\">da Costa BR, Cevallos M, Altman DG, <em>et al</em>. Uses and misuses of the STROBE statement: bibliographic study. <em>BMJ Open</em> 2011;<strong>1</strong>:e000048. doi:<a href=\"http://dx.doi.org/10.1136/bmjopen-2010-000048\">10.1136/bmjopen-2010-000048</a></span></p>\n<p><span style=\"font-family: Calibri;color: #000000;font-size: medium\">Cohen JI, Yates KF, Duong M, <em>et al</em>. Obesity, orbitofrontal structure and function are associated with food choice: a cross-sectional study. <em>BMJ Open</em> 2011;<strong>1</strong>:e000175. doi:<a href=\"http://dx.doi.org/10.1136/bmjopen-2011-000175\">10.1136/bmjopen-2011-000175</a></span></p>\n<p><span style=\"font-family: Calibri;color: #000000;font-size: medium\">Hotchkiss JW, Davies C, Gray L, <em>et al</em>. Trends in adult cardiovascular disease risk factors and their socio-economic patterning in the Scottish population 1995–2008: cross-sectional surveys. <em>BMJ Open</em> 2011;<strong>1</strong>:e000176. doi:<a href=\"http://dx.doi.org/10.1136/bmjopen-2011-000176\">10.1136/bmjopen-2011-000176</a></span><span style=\"font-family: Calibri;color: #000000;font-size: medium\"> </span><!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"6a8953fb-2ac6-5fc1-b588-b4a152c51679"}}

